<template>
  <div class="container mx-auto">
    <h2 class="text-yellow-500 text-lg uppercase p-4">
      Popular Actors
    </h2>
    <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
      <Actoritem v-for="actor in this.actors" :key="actor.id" :actor="actor"/>
    </div>
    <div class="flex justify-center mx-8 cursor-pointer py-8 font-bold uppercase">
    <div class="text-center mr-8" @click="previous()">
      previous
    </div>
    <div class="text-center" @click="next()">
      next
    </div>
    </div>
  <h3 v-if="bottomScroll" class="animate-pulse text-center p-8 font-bold">Loading...</h3>
  </div>
</template>

<script>
import Actoritem from '../components/actor/Actoritem.vue'
import axios from 'axios'
let currentPage = 1;
export default {
  components: { Actoritem },
  data() {
    return {
      actors: [],
      bottomScroll: false
    }
  },
  mounted() {
    this.getActors(currentPage)
    // this.scroll()
  },
  methods: {
    async getActors(page) {
      try{
        const response = await axios.get(`https://api.themoviedb.org/3/person/popular?page=${page}&api_key=04c35731a5ee918f014970082a0088b1&language=en-US`)
        this.actors = response.data.results
      } catch (err) {
        console.log(err)
      }
    },
    scroll() {
      window.onscroll = () => {
        let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight

        if(bottomOfWindow){
          currentPage += 1
          this.bottomScroll = true
          setTimeout(() => {
            this.getActors((currentPage += 1))
          }, 2000)
        }
      }
    },
    next() {
      currentPage += 1
      return this.getActors(currentPage)
    },
    previous() {
      currentPage -= 1
      return this.getActors(currentPage)
    }
  }
}
</script>