<template>
  <div>
    <router-link :to="{ name : 'ActorDetails',params: { id: actor.id} }">
      <img :src="getActorImage" alt="">
      <h3>{{ actor.name }}</h3>
    </router-link>
    <!-- <div class="text-gray-500 inline-block" v-for="know in actor.known_for" :key="know.id">{{ know.title }} ,</div> -->
  </div>
</template>

<script>
export default {
  props: {
    actor: {
      required: true
    }
  },
  computed: {
    getActorImage(){
      if(this.actor.profile_path){
        return "https://image.tmdb.org/t/p/w1280/" + this.actor.profile_path
      } else {
        return "https://via.placeholder.com/300x450"
      }
    }
  }
}
</script>